import navBar from '@/components/nav-bar.vue';
import dividLine from '@/components/line.vue';
import {
	changeName,
	changePhoneyzm,
	changePhone
} from '@/api/user'
export default {
	data() {
		return {
			phone: '',
			pphone: '',
			code: '',
			isSend: false,
			yzm: '发送验证码'
		}
	},
	components: {
		navBar,
		dividLine
	},
	onLoad() {
		this._getUser()
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		_getUser() {
			try {
				const user = uni.getStorageSync('user');
				this.phone = user.phone
				let start = this.phone.substring(0, 3);
				let last = this.phone.substring(8, 12);
				this.pphone = `${start}*****${last}`
				console.log('user', this.phone)
			} catch (e) {
				console.log('ERROR', e.message)
			}
		},
		send() {
			if (!this.isSend) {
				changePhoneyzm(this.phone).then(res => {
					if (res.code == 200) {
						uni.showToast({
							title: '验证码已发送'
						})
						let timer = null;
						let cut = 60;
						this.isSend = true;
						timer = setInterval(() => {
							this.yzm = --cut + "S"
							if (cut == 0) {
								clearInterval(timer)
								this.yzm = '发送验证码'
								this.isSend = false
							}
						}, 1000)
					}
				})
			}
		},
		submit() {
			changePhone(this.phone, this.code).then(res => {
				if (res.code == 100) {
					uni.showToast({
						title: res.msg,
						icon:"none"
					})
					return
				}
				uni.showToast({
					title: '验证成功'
				})
				setTimeout(() => {
					uni.navigateTo({
						url: '/pages/index/new-phone'
					})
				}, 500)

			})
		}
	},
}
